import XInputTypes from './XInput.types'
import S from './XInput.styles'

const XInput = (props: XInputTypes) => props.type === 'password' ? (
  <S.Password show={props.show} onClick={props.toggleShow}>
    <S.XInput {...props} />
  </S.Password>
) : (
  <S.XInput {...props} />
)

export default XInput